
import {ref, watch, computed} from 'vue'
import { useStore } from 'vuex'
export default {
    name: 'Footer',
    setup(){
        //vuex
        const store = useStore();
        function newWindow(url:any){
            window.open(url);
        }
        return {
            lang_code: computed(() => store.state.lang_code),
            newWindow,
        }
    }
}
